import React from 'react'
import './Missions.css';
import logo from '../../images/logo.svg';
import aws from '../../images/AWS.png';
import txsocial from '../../images/TX-SOCIAL.jpg';


function App() {
    return (

        
      <div className="App">
        
        <h1 class="missions">Missions confiées</h1>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p class="p1missions">La mission principale est de coder un site web en React</p> 
          <p class="p2missions">avant cela nous avons pris connaissance du logiciel TX-Social et de son environnement.</p> 
          <p class="p3missions">Et ensuite nous avons pris connaissance de l'environnement AWS.</p>
          <img src={aws} className="aws"/>
          <img src={txsocial} className="txsocial"/>
        </header>
      </div>
    );
  }
  
  export default App;

